// 作为vue的插件用来获取和设置localStorage
let store = {
    save(key,value){
        localStorage.setItem(key,JSON.stringify(value));
    },
    fetch(key){
        return JSON.parse(localStorage.getItem(key)) || {};
    }
}

export default {
    install:function(vm){
        vm.prototype.$local = store
    }
}



