import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/home'
// import Layout from '@/views/layout'
// import Project from '@/views/children/project'
// import Desk from '@/views/children/desk'
// import Document from '@/views/children/document'
// import Login from '@/components/login'
Vue.use(Router)

const Layout = () => import('@/views/layout')
const Project = () => import('@/views/children/project')
const Document = () => import('@/views/children/document')
const Desk = () => import('@/views/children/desk')
const Login = () => import('@/components/login')

const router =  new Router({
  routes: [
    {
      path:'/',
      component:Home
    },
    {
      path:'/management',
      component:Layout,
      children:[
        {
          path:'/project',
          name:'Project',
          component:Project,
          meta:{
            login:true
          }
        },
        {
          path:'/desk',
          name:'Desk',
          component:Desk,
          meta:{
            login:true
          }
        },
        {
          path:'/document',
          name:'Document',
          component:Document,
          meta:{
            login:false //不需要登录
          }
        }
      ]
    },
    {
      path:'/login',
      name:'Login',
      component:Login
    }
  ]
})
export default router
 router.beforeEach((to,from,next)=>{// 给需要登录的路由设置元信息meta
  let newBl = to.matched.some((item)=>{ 
    return item.meta.login
  })
  if(newBl){
    let info = router.app.$local.fetch('miaov') // router.app=this
    if(info.login){
      next()
    }else{
      next({ 
        path: '/login', 
        query:{ // 查询字符串，当需要登录的时候跳转到登录页面，并且记录下当前路由，当登录成功就直接跳转当前路由
          redirect:to.path.slice(1)
        } 
      })
     
    }
  }else{
    next()
  }
})


