<template>
    <div>
        <div class="header">
            <img src="../assets/miaov.png" alt="">
            <div>
                <router-link v-if="!isLogin" to="/login" tag="span">登录</router-link>
                <img v-if="isLogin" src="../assets/portrait.png" alt="">
            </div>
        </div>
        <div class="content">
            <p>一起学习Vue-router</p>
        </div>
        <div class="link">
            <router-link to="/management">我的项目</router-link>
            <router-link to="/document">我的文档</router-link>
        </div>
    </div>
</template>
<script>
export default {
    data(){
        return{
            isLogin:false
        }
    },
    created(){// 进入页面当数据加载完后用$local判断是否登录
        let info = this.$local.fetch('miaov');
        this.isLogin = info.login;
    }
}
</script>

<style scoped>
    .header div img{
        width:30px;
        height:30px;
        border:1px solid #fff;
        border-radius:50%;
    }
    .header{
        width:70%;
        /* border:1px solid #000; */
        margin:0 auto;
        padding:15px 0;
    }
    .header img{
        float:left;
    }
    .header span{
        background-color:dimgray;
        display:inline-block;
        padding:5px;
        color:#fff;
        border-radius:5px;
        text-align:center;
        cursor:pointer;
    }
    .header div{
        float:right;
    }
    .header::after{
        content:'';
        display:inline-block;
        clear:both;
    }
    body{
        margin:0;
        background-color:ghostwhite;
    }
    .content{
        /* border:1px solid red; */
        font-style:arial;
        font-size:30px;
        text-align:center;
        margin-top:50px;
    }
    .link{
        /* border:1px solid red; */
        text-align:center;
        margin-top:100px;
    }
    .link a{
        /* border:1px solid #000; */
        padding:8px 15px;
        border-radius:20px;
        background-color:mediumseagreen;
        color:#fff;
        text-decoration:none;
    }
    .link a:first-child{
        margin-right:25px;
    }
    .link a:last-child{
        margin-left:25px;
    }
    .link a:hover{
        background-color:limegreen;
        color:#fff;
    }
</style>



